<template>
	<div>
		<div v-if="!show">
			<input type="text" v-model="account" placeholder="请输入账号"/>
			<input type="password" v-model="password" placeholder="请输入密码"/>
		</div>
		<div v-else>
			<input type="text" v-model="phone" placeholder="请输入手机号"/>
			<button @click="send" :disabled='disabled'>{{sendout}}</button>
			<input type="text" v-model="code" placeholder="验证码"/>
			<input type="password" v-model="pw" placeholder="密码"/>
			<input type="password" v-model="password" placeholder="确认密码"/>
		</div>
		<div v-if="!show">
			<button @click="submit" style="width: 100%;" >登陆</button>
			<button style="width: 100%;" @click="show = true">注册</button>			
		</div>
		<div v-else>
			<button @click="register" style="width: 100%;" >注册</button>
			<button style="width: 100%;" v-if="show" @click="show = false">返回</button>
		</div>
	</div>
</template>

<script>
	import {getData} from '../service/getData'
	
	export default {
		data() {
			return {
				account: '',
				password: '',
				phone: '',
				code: '',
				show: false,
				sendout: '发送验证码',
				disabled: false,
				pw: ''
			}
		},
		methods: {
			submit() {
				getData('/user/appLogin', {
					namePhone: this.account,
					password: this.password
				}).then(d => {
					if(d.code == 100) {
						sessionStorage.token = d.object.token
						sessionStorage.userId = d.object.user.userId
						this.route('index')
					}
				})
			},
			register() {
				if(this.pw != this.password) {
					alert('两次密码不正确')
					return false
				}
				var _this = this
				getData('/user/appRegist', {
					phone: this.phone,
					password: this.password,
					code: this.code
				}).then(d => {
					if(d.code == 100) {
						_this.show = false
					}
				}).catch(err => {
					console.log(err)
				})
			},
			send() {
				this.disabled = true
				let num = 60
				var _this = this
				if(this.phone == '') {
					alert('请填写手机号')
					this.disabled = false
					this.sendout = '发送验证码'
					return false
				}
				getData('/user/appSend', {
					userPhone: this.phone
				}).then(d => {
					var time = setInterval(() => {
						if(num == 0) {
							clearInterval(time)
							_this.disabled = false
							_this.sendout = '重新发送'
						} else {
							_this.sendout = --num						
						}
					}, 1000)					
				}).catch((err) => {
					alert('服务错误')
				})
			}
		}
	}
</script>

<style>
</style>


